//  Header
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

export default function Header() {

  const BackgroundBlock = styled.div`
  div.containerHeader{
    min-height: 5vh;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    background-color: white;

    
    padding-bottom: 15px;
    border-bottom: solid 2px #002F5A;
  }

  div.menu{
    grid-column-start: 3;
    grid-column-end: 4;
    padding-top: 10px;


    background-color: white;

    /**/
    img{
      width: 35px;
      height: 35px;
    }

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }

  div.blank{
    grid-column-start: 2;
    grid-column-end: 3;

    /**/
    padding-top: 5%;
    font-size: 18px;
    color: #002F5A;
    font-weight: bold;

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }

  div.logo{
    grid-column-start: 1;
    grid-column-end: 2;
    padding-top: 10px;

    background-color: white;

    img{
      width: 40px;
      height: 40px;
    }

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }
  `


  return (
    <BackgroundBlock>
      <div className='containerHeader'>
        <div className='logo'>
          <Link to={'/'}>
            <img src={require('../../media/structure/.png')} alt='' style={{width: "40px", height: "40px"}}/>
          </Link>
        </div>
        <div className='blank'>
          <Link className= 'blank' style={{textDecoration: 'none', color: '#002F5A'}} to={'/'}>
            <span> ?</span>
          </Link>
        </div>
        <Link style={{textDecoration: 'none' }} to={'/choose'}>
          <div className='menu'><img src={require('../../media/tab/.png')} alt=''/></div>
        </Link>
      </div>
    </BackgroundBlock>
  )
}

