//  footer: "" "" "" 3  
import React from 'react'
import styled from 'styled-components'

export default function OnlyPrevFooter() {

  const BackgroundBlock = styled.div`
  div.containerHeader{

    /*min-height: 5vh;*/
    margin-top: 20px;
    border-top: solid 2px #002F5A;
    padding-top: 10px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: white;

    z-index: 1;
  }

  div.prev{

    flex-grow: 0.5;
    background-color: white;
    text-align: left;

    /**/
    text-align: right;
    font-size: 8px;
    color: #002F5A;
    font-weight: bold;

    /* */
    img{
      width: 40px;
      height: 40px;
    }

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }

  div.home{
    flex-grow: 8;


    /**/
    text-align: center;
    font-size: 8px;
    color: #002F5A;
    font-weight: normal;

    /* */
    img{
      width: 40px;
      height: 40px;
    }
  }

  div.next{

    flex-grow: 0.5;


    /**/
    text-align: left;
    font-size: 8px;
    color: #002F5A;
    font-weight: normal;

    /* */
    img{
      width: 40px;
      height: 40px;
    }
  }
`


  return (
    <BackgroundBlock>
      <div className='containerHeader'>
        <div className='prev' onClick={() => { window.history.back(); }}><img src={require('../../media/tab/.png')} alt="" /></div>
        <div className='home'></div>
        <div className='next'></div>
      </div>
      
    </BackgroundBlock>
  )
}
