//  footer (,     )
import React from 'react'
import styled from 'styled-components'

export default function Footer() {

    const BackgroundBlock = styled.div`
    div.containerHeader{

      /*min-height: 5vh;*/
      margin-top: 20px;
      border-top: solid 2px #002F5A;
      padding-top: 10px;

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      background-color: white;

      z-index: 1;
    }

    div.title{
      flex-basis: 10%;
      flex-grow: 4;
      background-color: white;

      /**/
      padding-right:10px;
      text-align: right;
      font-size: 11px;
      color: #002F5A;
      font-weight: bold;
    }

    div.content{
      flex-grow: 5;


      /**/
      text-align: left;
      font-size: 11px;
      color: #002F5A;
      font-weight: normal;
    }
  `


  return (
    <BackgroundBlock>
      <div className='containerHeader'>
        <div className='title'><span><b>/:</b></span><br/><span><b>:</b></span></div>
        <div className='content'><span>, , , </span><br/><span>rangyun36@gmail.com</span></div>
      </div>
      
    </BackgroundBlock>
  )
}
