//  header (  )
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import 'bootstrap/dist/css/bootstrap.css';
import MyModal from './MyModal';
import axios from 'axios'

export default function Header() {
  //  ( :  false)
  const [login, setLogin] = useState(false);
  const [thisUser, setThisUser] = useState('');

  const [modalShow, setModalShow] = useState(false); //     

  //   
  useEffect( () =>{
    if(sessionStorage.getItem("user")!=null){
      setLogin(true);
    }
    else{
      setLogin(false);
    }

  })

  //   
  function ChooseOption(){
    // x (false)
    if( login === false ){
      // or   
      // window.location.href='http://localhost:3000/#/choose';
      return(
      <div className='menu'>
        <Link style={{textDecoration: 'none' }} to={'/choose'}>
          <img src={require('../../../media/tab/.png')} alt=''/>
        </Link>
       </div>
      )
    }

    //   (true)
    else{
      return(
        //     userdata 
        <div className='menu' onClick={()=>MyModal()}>
          <img src={require('../../../media/tab/.png')} alt=''/>
        </div>
      )
    }
  }


  return (
    <>
    <BackgroundBlock>
      <div className='containerHeader'>
        <div className='logo'>
          <Link to={'/'}>
            <img src={require('../../../media/structure/.png')} alt='' style={{width: "40px", height: "40px"}}/>
          </Link>
        </div>
        <div className='blank'>
          <Link className= 'blank' style={{textDecoration: 'none', color: '#002F5A'}} to={'/'}>
            <span> ?</span>
          </Link>
        </div>
        {
          login === false?
          <div className='menu'>
            <Link style={{textDecoration: 'none' }} to={'/choose'}>
              <img src={require('../../../media/tab/.png')} alt=''/>
            </Link>
          </div>
          :
          <div className='menu' onClick={()=> setModalShow(true)}>
            <img src={require('../../../media/tab/.png')} alt=''/>
          </div>
        }
      </div>
      <MyModal show={modalShow} onHide={() => setModalShow(false)} />
    </BackgroundBlock>

    
    </>
  )
}
// login? MyModal(thisUser): location.href='http://localhost:3000/#/choose'

const BackgroundBlock = styled.div`
  div.containerHeader{
    min-height: 3vh;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    background-color: white;

    
    padding-bottom: 15px;
    border-bottom: solid 2px #002F5A;
  }

  div.menu{
    grid-column-start: 3;
    grid-column-end: 4;
    padding-top: 10px;


    background-color: white;

    /**/
    img{
      width: 35px;
      height: 35px;
    }

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }

  div.blank{
    grid-column-start: 2;
    grid-column-end: 3;

    /**/
    padding-top: 5%;
    font-size: 18px;
    color: #002F5A;
    font-weight: bold;

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }

  div.logo{
    grid-column-start: 1;
    grid-column-end: 2;
    padding-top: 10px;

    background-color: white;

    img{
      width: 40px;
      height: 40px;
    }

    /**/
    &:hover {
      background-color: white;
      opacity: 0.6;
    }
  }
  `

// ={process.env.PUBLIC_URL +`${'/media/structure/2.jpg'}`