//CSS
const MainBlockStyle = styled.div`
form.container{
    display: grid;
    grid-template-columns: 1fr 1fr 4fr 1fr;
    grid-template-rows: 2fr 0.7fr 0.7fr 1fr 1fr 2fr;
    background-color: white;
    text-align: center;

    row-gap: 15px;
    column-gap: 1px;
    
    height: 75vh;
    width: 45vh;
  }

  /**/
  .ID{
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;


    /**/
    font-size: 17px;
    color: black;
    font-weight: bold;
  }

  /**/
  .IDBlock{
    /**/
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;

    /**/
    border-radius: 5px;

    /**/
    font-size: 14px;
    color: black;
    font-weight: normal;

    /**/
    &:hover {
        opacity: 0.7;
    }
  }

  /**/
  .PW{
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;



    /**/
    font-size: 17px;
    color: black;
    font-weight: bold;

  }

  /**/
  .PWBlock{
    /**/
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 4;


    /**/

    border-radius: 5px;

    /**/
    font-size: 14px;
    color: black;
    font-weight: normal;

    /**/
    &:hover {
        opacity: 0.7;
    }
  }

  /* */
  button.Login{
    /**/
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 5;

    /**/
    background-color: #002F5A;
    opacity: 0.8;

    /**/
    border-radius: 5px;


    /**/
    font-size: 19.5px;
    color: white;
    font-weight: bold;

    /**/
    &:hover {
      background-color: #002F5A;
      opacity: 0.9;
    }
  }

  /*  */
  .noticeLetter{
    font-size: 8px;
    color: #C4C4C4;

  }

  /*  */
  .resetPW{
    /**/
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 5;
    grid-row-end: 6;


    /**/
    border-radius: 5px;
    width: 80%;
    height: 60%;
    

    /**/
    font-size: 12px;
    color: white;
    font-weight: bold;

    /**/
    &:hover {
      background-color: #5a5a5a;
      opacity: 0.9;
    }
  }

  /* */
  .error{
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 6;
    grid-row-end: 7;

    /**/
    font-size: 12px;

  }
`
