import React, {useEffect, useState, useRef} from 'react';
import { useForm, Controller} from 'react-hook-form';
import styled from 'styled-components';
import axios from 'axios';
import Header from '../../../common/header/Header';
import OnlyPrevFooter from '../../../common/footer/OnlyPrevFooter';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Col,  Container, Modal, Row} from 'react-bootstrap';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import AuthService from '../../../services/auth.service';
import { isEmail } from "validator";
import { useNavigate } from 'react-router-dom';
import Select from 'react-bootstrap/FormSelect'//bootstrap   Select (  Form.select    )

//input    
const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         !
      </div>
    );
  }
};

//id()
const vuserstdNum = (value) => {
  if (value.length < 4 || value.length > 9) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
        / .
      </div>
    );
  }
};

//nickName
const vusername = (value) => {
  if (value.length < 2 || value.length > 10) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         4~10 .
      </div>
    );
  }
};

//password
const vpassword = (value) => {
  if (value.length < 6 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         6~20 .
      </div>
    );
  }
};

//key value 
function getKeyByValue(object, value) {
    return Object.keys(object).find(key => object[key] === value);
  }

export default function SignupForm() {
    //  
    const [totalFirstMajor, setTotalFirstMajor] = useState(''); //     
    const [totalDualMajor, setTotalDualMajor] = useState(''); //     
    const [userType, setUserType] = useState('mentee'); //   
    const [dualmajor, setDualmajor] = useState('');//      
    const [show, setShow] = useState(false);//  
    const [resign, setResign] = useState(false); //  

    const form = useRef();
    const checkBtn = useRef();
    const [username, setUsername] = useState("");
    const [userstdNum, setUserstdNum] = useState("");
    const [password, setPassword] = useState("");
    const [grade, setGrade] = useState("1");
    const [firstMajor,  setFirstMajor] = useState("GBT");
    const [dualMajor, setDualMajor] = useState("");
    const [successful, setSuccessful] = useState(false);
    const [message, setMessage] = useState("");

    // (call-back)
    let navigate = useNavigate();

    //  user 
    let thisUser = false;
    if(sessionStorage.getItem('user') !== null){
        thisUser = Object.values(JSON.parse(sessionStorage.getItem('user')));
    }



    //  value  
    useEffect(() => {
        //
        console.log("thisUser",thisUser);

        AuthService.firstMajorList();
        AuthService.dualMajorList()

        let allFirstMajor = false;
        let allDualMajor = false;
        if(localStorage.getItem('firstMajor') !== null){
            allFirstMajor = Object.values(JSON.parse(localStorage.getItem('firstMajor')));
        }
        if(localStorage.getItem('dualMajor') !== null){
            allDualMajor = Object.values(JSON.parse(localStorage.getItem('dualMajor')));
        }

        //    value   thisUser 
        if (thisUser !== false){
            //   (   !)
            setUserstdNum(thisUser[0]); ///
            setUsername(thisUser[1]);   //
            setGrade(thisUser[2]);      //
            setUserType(thisUser[3]);   // 
            setFirstMajor(thisUser[5]); // id
            setDualMajor(thisUser[7]);  //  id

            //  /    
            //
            // let allFirstMajor = JSON.parse(AuthService.firstMajorList())
            //   
            setTotalFirstMajor(allFirstMajor);
            
            //
            // let allDualMajor = JSON.parse(AuthService.dualMajorList())
            //   
            setTotalDualMajor(allDualMajor);             
            

          }
    },[])

    //   
    useEffect(() => {


        //   true 
        if( resign === true){
            AuthService.applyResign(userstdNum).then(
                (response)=>{
                    // 
                    console.log("  ");
                    
                    //
                    AuthService.logout();

                    //main page 
                    navigate("/");
                    window.location.reload();
                },
                (error) => {
                    alert(" . .");
                }
            )
        }
    }, [resign])

    //       
    // setTotalFirstMajor(exampleFirstMajor);
    // setTotalDualMajor(exampleDualMajor);

    //   
    const onChangeUsername = (e) => {
        const username = e.target.value;
        setUsername(username);
    };

    const onChangeUserstdNum = (e) => {
        const userstdNum = e.target.value;
        setUserstdNum(userstdNum);
    };

    const onChangePassword = (e) => {
        const password = e.target.value;
        setPassword(password);
    };

    //   
    //select    
    const onChangeUserGrade = (e) => {
        const userGrade = e.target.value;
        setGrade(userGrade);
    }

    const SelectedUserType= (selected) => {
        //   
        if(selected.target.value === "mento"){
        setUserType("mento");
        setDualmajor('()');
        }
        //   
        else{
        setUserType("mentee");
        setDualmajor('');
        }
    }

    const onChangeUserFirstMajor = (e) =>{
        const userFirstMajor = e.target.value;
        setFirstMajor(userFirstMajor);
    }

    const onChangeUserDualMajor = (e) =>{
        const userDualMajor = e.target.value;
        setDualMajor(userDualMajor);
    }

    //    API  
    const handleRegister = (e) => {
        e.preventDefault();
        setMessage("");
        setSuccessful(false);
        form.current.validateAll();
        if (checkBtn.current.context._errors.length === 0) {
        AuthService.changeInfo(userstdNum, password, username, grade, userType, firstMajor, dualMajor).then(
            (response) => {
            setMessage(response.data.message);
            setSuccessful(true);
            
            let newUser = {"stdNum":userstdNum, "nickName": username, "grade": grade, "userType": userType, "firstMajor": firstMajor, "daulMajor": dualMajor};
            // 
            sessionStorage.setItem("user", JSON.stringify(newUser));

            //main page 
            navigate("/");
            window.location.reload();
            },
            (error) => {
            const resMessage =
                "  ."
                // (error.response &&
                //   error.response.data &&
                //   error.response.data.message) ||
                // error.message ||
                // error.toString();
            setMessage(resMessage);
            setSuccessful(false);
            }
        );
        }
    };

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <MainBlockStyle>
        <div className="mainContainer">
            <div className="header"><Header/></div>
            <FormBlockStyle>

            <Form className= "container" onSubmit={handleRegister} ref={form}>
                {!successful && (
                <Container>
                <div>
                    <Row>
                    <Col  md={12} xs={12}>
                    <span className='comment'> </span>
                    </Col>
                    </Row>
                    
                    <hr/>
                    <Row>
                    <Col md={5} xs={5}>
                        <span className='titleStyle'>/</span>
                    </Col>
                    <Col md={7} xs={7}>
                        <Input
                        type="userstdNum"
                        className="form-control"
                        name="userstdNum"
                        value={userstdNum}
                        onChange={onChangeUserstdNum}
                        validations={[required, vuserstdNum]}
                        disabled
                        />
                    </Col>
                    </Row>
                    <Row>
                    <Col md={5} xs={5}>
                        <span className='titleStyle'></span>
                    </Col>
                    <Col md={7} xs={7}>
                        <Input
                            type="username"
                            className="form-control"
                            name="username"
                            value={username}
                            onChange={onChangeUsername}
                            validations={[required, vusername]}
                        />
                    </Col>
                    </Row>
                    <Row>
                    <Col md={5} xs={5}>
                        <span className='titleStyle'></span>
                    </Col>
                    <Col md={7} xs={7}>
                        <Input
                        type="password"
                        className="form-control"
                        name="password"
                        value={password}
                        onChange={onChangePassword}
                        validations={[required, vpassword]}
                        />
                    </Col>
                    </Row>

                    <br/>
                    <Row>
                    <Col md={5} xs={12}>
                        <span className='titleStyle'></span>
                    </Col>
                    <Col md={7} xs={12}>
                        <Select className='inputStyle' onChange={onChangeUserFirstMajor}>
                        {
                        !totalFirstMajor?  
                        <option value="0"> </option>:
                        totalFirstMajor.map(thisMajor => (
                            <option key={thisMajor.id} value={thisMajor.id}>
                            {thisMajor.name}
                            </option>
                        ))
                        }
                        </Select>
                    </Col>
                    </Row>

                    <Row>
                    <Col md={5} xs={12}>
                        <span className='titleStyle'></span>
                    </Col>
                    <Col md={7} xs={12}>
                        <Select className='inputStyle' onChange={onChangeUserGrade}>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4 ">4 </option>
                        </Select>
                    </Col>
                    </Row>

                    <Row>
                    <Col md={5} xs={12}>
                        <span className='titleStyle'></span>
                    </Col>
                    <Col md={7} xs={12}>
                        <Select className='inputStyle' onChange={SelectedUserType}>
                        <option value="mentee"></option>
                        <option value="mento"></option>
                        </Select>
                    </Col>
                    </Row>

                    <Row>
                    <Col md={5} xs={12}>
                        <span className='titleStyle'>{dualmajor}</span>
                    </Col>
                    <Col md={7} xs={12}>
                        <Select className='inputStyle' onChange={onChangeUserDualMajor}>
                        {
                        !totalDualMajor?  
                        <option value="0"> </option>:
                        totalDualMajor.map(thisMajor => (
                            <option key={thisMajor.name} value={thisMajor.name}>
                            {thisMajor.name}
                            </option>
                        ))
                        }
                        </Select> 
                    </Col>
                    </Row>
                </div>
                </Container>
                )}

                <br/>
                <Container>
                <Row>
                    <Col md={6} xs={6}>
                        <Button type='button' className='buttonContract' onClick={handleShow}>
                            
                        </Button>
                    </Col>
                    <Col md={6} xs={6}>
                        <Button className='buttonRegister' type="submit">
                            
                        </Button>
                    </Col>
                </Row>

                {message && (
                    <div className="form-group">
                    <div
                        className={ successful ? "alert alert-success" : "alert alert-danger" }
                        role="alert"
                    >
                        {message}
                    </div>
                    </div>
                )}
                <CheckButton style={{ display: "none" }} ref={checkBtn} />
                </Container>
            </Form>   
            
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
                </Modal.Header>
                <Modal.Body>' ?   ?</Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={ () => {
                    handleClose();
                    setResign(true);
                    // setConfirm(true);
                }}>
                    
                </Button>
                <Button style={{backgroundColor: "#002F5A", opacity: "0.8"}} onClick={() => {
                    handleClose();
                    // setConfirm(false);
                }}>
                    
                </Button>
                </Modal.Footer>
            </Modal>

            </FormBlockStyle>
            <div className='footer'><OnlyPrevFooter/></div>
        </div>
        </MainBlockStyle>
    )
    }


const FormBlockStyle = styled.div`
.container{

    background-color: white;


  }

  /* */
  .comment{

    /*padding-top:;*/
    padding-left: 20px;
    padding-right: 20px;

    /**/
    font-size: 12px;
    color: black;
    font-weight: bold;
  }

  /*form  */
  .formMain{

    text-align: left;
  }


  .formBlock{

    /**/

    /**/
    font-size: 12px;
    color: black;
    font-weight: bold;
    text-align: left;

  }
  /*  */
  .titleStyle{

    /**/
    font-size: 12px;
    color: black;
    font-weight: bold;
    text-align: left;

    width: 40%;
  }

  /* */
  .inputStyle{
    /**/

    border-radius: 5px;

    /**/
    font-size: 11px;
    color: black;
    font-weight: bold;
    text-align: left;



    /**/
    &:hover {
        opacity: 0.7;
    }

  }

  /* */
  .contract{

    /**/
    font-size: 12px;
    color: black;
    font-weight: bold;


  }

  /*  */
  .buttonContract{

    /**/
    margin-right: 5px;
    border-radius: 5px;


    font-size: 14px;
    color: white;

    /**/
    background-color: #5a5a5a;
    opacity: 1;

    /**/
    &:hover {
        opacity: 0.9;
    }
  }

  /*     */
  .registerNotice{

    font-size: 11px;
    color: #C4C4C4;
    font-weight: bold;
    
  }

  /* */
  .buttonRegister{
    /**/

    background-color: #002F5A;
    opacity: 0.8;

    /**/
    border-radius: 5px;

    /**/
    font-size: 14px;
    color: white;
    font-weight: bold;

    /**/
    &:hover {
      background-color: #002F5A;
      opacity: 0.9;
    }
  }

`
const MainBlockStyle = styled.div`
    
div.mainContainer{
  display: grid;
  grid-template-rows: 0.9fr 6fr 1fr;
  background-color: white;
  text-align: center;
  justify-content: center;
  vertical-align: middle;
  
  /*border: solid 1px #002F5A;*/

  z-index:0;
}


div.header{
  gird-row-start: 0;
  grid-row-start: 1;

  z-index:1;
}

div.mainBody{
  gird-row-start: 1;
  grid-row-start: 2;
}

div.footer{
  margin-top:10px;
  bottom:0px;
  gird-row-start: 2;
  grid-row-start: 3;
  z-index:1;
}
`





